.detailSection {
  width: 60%;
  display: flex;
  flex-direction: row;
  align-items: center;
  border: solid 1px gray;
  border-radius: 2rem;
  margin: 3rem;

  @media (max-width: 480px) {
    flex-direction: column;
  }
}

.textDetailContainer {
  margin-bottom: 2rem;
}
.imgDetail {
  width: 30rem;
  height: 40rem;
  border-radius: 2rem;
  margin: 2rem;

  @media (max-width: 480px) {
    width: 15rem;
    height: 20rem;
  }
}

.titleDetail {
  font-size: 3rem;
  margin-bottom: 1rem;

  @media (max-width: 480px) {
    font-size: 2rem;
  }
}

.textDetail {
  font-size: 2.4rem;
  margin-bottom: 0.5rem;

  @media (max-width: 480px) {
    font-size: 1.5rem;
  }
}

.backBtnDetail {
  text-decoration: none;
  color: black;
  font-size: 2rem;
  border: solid 1px gray;
  border-radius: 1rem;
  padding: 1rem;
  margin-left: 3.5rem;
}

.imgHouseDetail {
  width: 15rem;
  height: 15rem;
}
